"use strict";
import StepCounter from "components/InputStepCounter";
import { AutoRow } from "components/deprecated/Row";
import { Bound } from "state/mint/v3/actions";
import { Trans } from "uniswap/src/i18n";
export default function RangeSelector({
  priceLower,
  priceUpper,
  onLeftRangeInput,
  onRightRangeInput,
  getDecrementLower,
  getIncrementLower,
  getDecrementUpper,
  getIncrementUpper,
  currencyA,
  currencyB,
  feeAmount,
  ticksAtLimit
}) {
  const tokenA = (currencyA ?? void 0)?.wrapped;
  const tokenB = (currencyB ?? void 0)?.wrapped;
  const isSorted = tokenA && tokenB && tokenA.sortsBefore(tokenB);
  const leftPrice = isSorted ? priceLower : priceUpper?.invert();
  const rightPrice = isSorted ? priceUpper : priceLower?.invert();
  return <AutoRow gap="md">
    <StepCounter
      value={ticksAtLimit[isSorted ? Bound.LOWER : Bound.UPPER] ? "0" : leftPrice?.toSignificant(8) ?? ""}
      onUserInput={onLeftRangeInput}
      decrement={isSorted ? getDecrementLower : getIncrementUpper}
      increment={isSorted ? getIncrementLower : getDecrementUpper}
      decrementDisabled={leftPrice === void 0 || ticksAtLimit[isSorted ? Bound.LOWER : Bound.UPPER]}
      incrementDisabled={leftPrice === void 0 || ticksAtLimit[isSorted ? Bound.LOWER : Bound.UPPER]}
      feeAmount={feeAmount}
      label={leftPrice ? `${currencyB?.symbol}` : "-"}
      title={<Trans i18nKey="common.lowPrice" />}
      tokenA={currencyA?.symbol}
      tokenB={currencyB?.symbol}
    />
    <StepCounter
      value={ticksAtLimit[isSorted ? Bound.UPPER : Bound.LOWER] ? "\u221E" : rightPrice?.toSignificant(8) ?? ""}
      onUserInput={onRightRangeInput}
      decrement={isSorted ? getDecrementUpper : getIncrementLower}
      increment={isSorted ? getIncrementUpper : getDecrementLower}
      incrementDisabled={rightPrice === void 0 || ticksAtLimit[isSorted ? Bound.UPPER : Bound.LOWER]}
      decrementDisabled={rightPrice === void 0 || ticksAtLimit[isSorted ? Bound.UPPER : Bound.LOWER]}
      feeAmount={feeAmount}
      label={rightPrice ? `${currencyB?.symbol}` : "-"}
      tokenA={currencyA?.symbol}
      tokenB={currencyB?.symbol}
      title={<Trans i18nKey="common.highPrice" />}
    />
  </AutoRow>;
}
