"use strict";
import Badge from "components/Badge/Badge";
import { PoolState } from "hooks/usePools";
import { ThemedText } from "theme/components";
import { Trans } from "uniswap/src/i18n";
export function FeeTierPercentageBadge({
  feeAmount,
  distributions,
  poolState
}) {
  const pct = distributions?.[feeAmount]?.toFixed(0);
  return <Badge><ThemedText.DeprecatedLabel fontSize={10}>{!distributions || poolState === PoolState.NOT_EXISTS || poolState === PoolState.INVALID ? <Trans i18nKey="common.notCreated.label" /> : distributions[feeAmount] !== void 0 ? <Trans i18nKey="fee.selectPercent" values={{ pct }} /> : <Trans i18nKey="common.noData" />}</ThemedText.DeprecatedLabel></Badge>;
}
