"use strict";
import { ButtonGray } from "components/Button/buttons";
import { OutlineCard } from "components/Card/cards";
import { AutoColumn } from "components/deprecated/Column";
import { Input as NumericalInput } from "components/NumericalInput";
import styled, { keyframes } from "lib/styled-components";
import { useCallback, useEffect, useState } from "react";
import { Minus, Plus } from "react-feather";
import { ThemedText } from "theme/components";
import { styled as tamaguiStyled, Text } from "ui/src";
import { Trans } from "uniswap/src/i18n";
const pulse = (color) => keyframes`
  0% {
    box-shadow: 0 0 0 0 ${color};
  }

  70% {
    box-shadow: 0 0 0 2px ${color};
  }

  100% {
    box-shadow: 0 0 0 0 ${color};
  }
`;
const InputRow = styled.div`
  display: flex;
`;
const SmallButton = styled(ButtonGray)`
  border-radius: 8px;
  padding: 4px;
`;
const FocusedOutlineCard = styled(OutlineCard)`
  border-color: ${({ active, theme }) => active && theme.deprecated_stateOverlayPressed};
  padding: 12px;
  animation: ${({ pulsing, theme }) => pulsing && pulse(theme.accent1)} 0.8s linear;
`;
const StyledInput = styled(NumericalInput)`
  background-color: transparent;
  font-weight: 535;
  text-align: left;
  width: 100%;

  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToSmall`
    font-size: 16px;
  `};
`;
const InputColumn = styled(AutoColumn)`
  width: 100%;
`;
const InputTitle = tamaguiStyled(Text, {
  fontSize: 12,
  fontWeight: "$medium",
  color: "$neutral2"
});
const ButtonLabel = styled(ThemedText.DeprecatedWhite)`
  color: ${({ theme, disabled }) => disabled ? theme.neutral2 : theme.neutral1} !important;
  display: flex;
`;
const StepCounter = ({
  value,
  decrement,
  increment,
  decrementDisabled = false,
  incrementDisabled = false,
  width,
  locked,
  onUserInput,
  title,
  tokenA,
  tokenB
}) => {
  const [active, setActive] = useState(false);
  const [localValue, setLocalValue] = useState("");
  const [useLocalValue, setUseLocalValue] = useState(false);
  const [pulsing, setPulsing] = useState(false);
  const handleOnFocus = () => {
    setUseLocalValue(true);
    setActive(true);
  };
  const handleOnBlur = useCallback(() => {
    setUseLocalValue(false);
    setActive(false);
    onUserInput(localValue);
  }, [localValue, onUserInput]);
  const handleDecrement = useCallback(() => {
    setUseLocalValue(false);
    onUserInput(decrement());
  }, [decrement, onUserInput]);
  const handleIncrement = useCallback(() => {
    setUseLocalValue(false);
    onUserInput(increment());
  }, [increment, onUserInput]);
  useEffect(() => {
    if (localValue !== value && !useLocalValue) {
      setTimeout(() => {
        setLocalValue(value);
        setPulsing(true);
        setTimeout(function() {
          setPulsing(false);
        }, 1800);
      }, 0);
    }
  }, [localValue, useLocalValue, value]);
  return <FocusedOutlineCard pulsing={pulsing} active={active} onFocus={handleOnFocus} onBlur={handleOnBlur} width={width}><InputRow>
    <InputColumn justify="flex-start">
      <InputTitle fontSize={12} textAlign="center">{title}</InputTitle>
      <StyledInput
        className="rate-input-0"
        value={localValue}
        fontSize="20px"
        disabled={locked}
        onUserInput={(val) => {
          setLocalValue(val);
        }}
      />
      <InputTitle fontSize={12} textAlign="left"><Trans i18nKey="common.feesEarnedPerBase" values={{ symbolA: tokenB, symbolB: tokenA }} /></InputTitle>
    </InputColumn>
    <AutoColumn gap="8px">
      {!locked && <SmallButton data-testid="increment-price-range" onClick={handleIncrement} disabled={incrementDisabled}><ButtonLabel disabled={incrementDisabled} fontSize="12px"><Plus size={18} /></ButtonLabel></SmallButton>}
      {!locked && <SmallButton data-testid="decrement-price-range" onClick={handleDecrement} disabled={decrementDisabled}><ButtonLabel disabled={decrementDisabled} fontSize="12px"><Minus size={18} /></ButtonLabel></SmallButton>}
    </AutoColumn>
  </InputRow></FocusedOutlineCard>;
};
export default StepCounter;
